.home {
  text-align: center;
  background: #4b6cb7;
  background: linear-gradient(to right, #182848, #4b6cb7);
  height: 100vh;
}

h1 {
  position: relative;
  z-index: 2;
  font-size: 3rem;
  font-weight: 900;
  color: #fff;
  font-family: "Marcellus SC", serif;
  background: #4b6cb7;
  background: linear-gradient(to bottom, #6d110e, #0e1014);
  border: 6px solid black;
  border-radius: 10px;
  margin: 2.5rem 0 0.1rem;
  padding: 0.1rem 0.4rem;
  display: inline-block;
}

p {
  margin: 0;
  font-size: 0.95rem;
}

.imageMapImage {
  display: -webkit-flex;
  display: flex;
  height: 7rem;
  width: 95%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: bottom;
          align-items: bottom;
  margin: 0.4rem auto;
}

#nissan {
  margin-top: auto;
}

#lexus {
  margin-bottom: auto;
}

#mazda {
  margin-bottom: auto;
}

#acura {
  margin-top: auto;
}

img {
  width: 10%;
  margin-top: 5vh;
  position: relative;
  z-index: 2;
  object-fit: contain;
  border-radius: 5px;
}

#phone {
  display: inline-block;
  color: white;
  background: #000000;
  background: linear-gradient(to bottom, #212122, #070707);
  border: 2px solid #fff;
  border-radius: 10px;
  padding: 0.3rem 1rem;
  margin: 5px 0 5px;
  letter-spacing: 2px;
  font-size: 1.3rem;
  font-weight: 800;
  text-decoration: none;
  position: relative;
  z-index: 2;
  transition: all 0.3s ease;
}

#phone:hover {
  cursor: pointer;
  background: #2e8b57;
  color: #000000;
  font-weight: bold;
  border: 2px solid #000000;
}

#address {
  background-color: #4e5456;
  position: relative;
  z-index: 2;
  display: inline-block;
  padding: 0.5%;
  border: 3px solid black;
  border-radius: 4px;
  font-weight: 900;
  margin-bottom: 0;
}

#slogan {
  margin-top: 2.4rem;
  display: inline-block;
  z-index: 2;
  position: relative;
  padding: 0.5%;
  background-color: #4e5456;
  border: 5px solid black;
  border-radius: 3px;
  font-weight: 900;
}

#bottom {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  margin: 0.8rem 0 0.5rem;
}

#hoursBox {
  position: relative;
  z-index: 2;
  background-color: #4e5456;
  border: 4px solid black;
  border-radius: 3px;
  padding: 0 0.4%;
}

#hours {
  font-weight: 900;
}

h5 {
  color: #ffffff !important;
  background-color: #000000;
}

