#reviews {
  position: relative;
  z-index: 2;
  background-color: #4e5456;
  border: 4px solid black;
  border-radius: 5px;
  padding: 0.5%;
}

h5 {
  display: inline-block;
  position: relative;
  z-index: 2;
  margin: 0.5rem 0 0.7rem;
  color: darkred;
  border: 3px solid black;
  border-radius: 3px;
  padding: 0.1rem 0.3rem;
  font-weight: 900;
  margin-bottom: 0.2rem;
}

#rating,
#number {
  color: #fff;
}

p {
  color: #000000 !important;
  font-weight: 900;
}

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0.2rem 0;
  background-color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 0;
}

#invisible {
  display: none;
}

#brand {
  font-size: 0.8rem;
  margin-left: auto;
}

#justice {
  text-decoration: none;
}

#copyright {
  margin-right: 0.3rem;
}

div a {
  margin: 0 0.3rem;
}

span {
  font-size: 1.7rem;
  color: blue;
}

@media screen and (min-width: 768px) {
  #invisible {
    display: inline-block;
    margin-right: auto;
    visibility: hidden;
  }
}

div {
  text-align: center;
}

