#form {
  width: 70%;
  text-align: center;
  position: absolute;
  padding: 0.5rem;
  top: 50%;
  right: 50%;
  -webkit-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
}

h3 {
  border: 4px solid #000000;
  display: inline-block;
  padding: 1.5%;
  color: #000000;
  border-radius: 5px;
  background: #000;
  font-weight: 900;
  font-size: 1.8rem;
  color: white;
}

input {
  display: inline-block;
  border-radius: 4px;
  margin: 0.7%;
  border: 3px solid #0a0909;
}

textarea {
  border: 2px solid #050505;
}

button {
  background-color: rgb(106, 2, 2);
  color: #fff;
  font-size: 1.3rem;
  height: 2.5rem;
  width: 7rem;
  border: 5px solid black;
  border-radius: 10px;
  margin-top: 1rem;
}

button:hover {
  cursor: pointer;
  background-color: rgb(79, 4, 4);
}

body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  background-color: #1347a0;
  font-family: "Lora", serif;
  overflow: hidden;
}

